.user__field {
  width: 90%;
  background-color: white;
  height: auto;
  margin: 0 auto;
  margin-top: 20px;
  padding-bottom: 20px;
  text-align: center;
}

.comment-action {
  padding-left: 8px;
  cursor: 'auto';
}

[class*='-col-rtl'] .comment-action {
  padding-right: 8px;
  padding-left: 0;
}



.ant-comment-avatar {
  width: 80px;
  height: 80px;
  padding-left: 30px;
  span {
    width: 70px;
    height: 70px;
  }
  img {
    width: 70px;
  height: 70px;
  }
}

.ant-comment-content-author-name {
  p {
    font-size: 20px;
    padding-left: 20px;
    font-weight: bold;
    text-align: center;
  }
}

.ant-collapse {
  width: 80%;
  margin: 0 auto;
}

.ant-comment-content-detail, .ant-collapse-header, .ant-collapse-content-box {
  text-align: left;
}

.ant-comment-content-detail {
  p {
    width: 80%;
    margin: 0 auto;
    white-space: normal;
    font-size: 18px;
    padding: 10px;
  }
}

.ant-card-body{
  text-align: left;
  > p {
    padding-left: 60px;
  }
}

.ant-card-bordered {
  margin: 0 auto;
}